* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: grid;
  align-items: center;
  justify-content: center;
  height: 100vh;
  overflow-y: scroll;
  font-family: Karla;
  background: #f5f7fb;
}

.wrap button,
.quiz-wrap button {
  font-family: inter;
  border: none;
  border-radius: 15px;
  background: #4d5b9e;
  color: white;
  font-weight: 500;
  padding: 1rem 1.8rem;
  width: 193px;
  margin: 0 auto;
  cursor: pointer;
  font-size: inherit;
}

.wrap {
  font-size: 1rem;
  max-width: 550px;
  padding: 0 1.5rem;
  text-align: center;
  margin: 2rem 0;
}
.wrap .start-page h1 {
  font-size: 32px;
  line-height: 36px;
}
.wrap .start-page .text {
  font-family: inter;
  line-height: 19.26px;
  font-weight: 400;
  margin: 0.5rem 0 0.8rem 0;
}

.quiz-wrap {
  margin-top: 1rem;
}
.quiz-wrap button {
  width: 120px;
  padding: 10px 0;
  border-radius: 10px;
  font-size: 10px;
}
.quiz-wrap .quiz {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
}
.quiz-wrap .quiz + hr {
  border: 0.794239px solid #dbdef0;
  margin: 1rem 0;
}
.quiz-wrap .quiz .options {
  display: flex;
  gap: 1rem;
  padding-top: 14px;
  flex-wrap: wrap;
  font-family: Inter;
  font-weight: 500;
}
.quiz-wrap .quiz .options .label {
  border: 1px solid #4d5b9e;
  border-radius: 8px;
  padding: 0.5rem 2rem;
  display: block;
  line-height: 18px;
  cursor: pointer;
}
.quiz-wrap .quiz .options .label.rest {
  opacity: 0.5;
}
.quiz-wrap .quiz .options .label.true {
  border-color: #94d7a2;
  background: #94d7a2;
  color: #293264;
}
.quiz-wrap .quiz .options input {
  text-align: left;
  display: none;
}
.quiz-wrap .quiz .options input:checked + .label {
  background: #d6dbf5;
  color: #293264;
  border-color: #d6dbf5;
}
.quiz-wrap .quiz .options input:checked + .label.true {
  border-color: #94d7a2;
  background: #94d7a2;
  color: #293264;
}
.quiz-wrap .quiz .options input:checked + .label.false {
  opacity: 0.5;
  background: #f8bcbc;
  color: #293264;
}

.wrap .q-link {
  text-decoration: none;
  font-size: 1.5rem;
}

.score-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}
.score-wrap .score {
  font-family: inter;
  font-weight: 700;
}
.score-wrap button {
  margin: 0;
  margin-left: 1rem;
  font-size: 1rem;
  width: max-content;
  padding: 1rem;
}

@media screen and (min-width: 800px) {
  .wrap {
    width: 60vw;
    max-width: 700px;
    margin: 1rem 0 3rem 0;
  }
}